import { AbstractFile } from "@observablehq/stdlib";
// import { Runtime } from "@observablehq/runtime";
// export const load = (name: string) => {
// };
class FA extends AbstractFile {
    constructor(name) {
        super(name, name);
    }
    url() {
        return this.name;
    }
}
export const loadSource = (url) => new FA(url);
